<template>
    <div class="spXiangqin">
      <div class="xiang_wrap">
        <div class="swiper-container">
          <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="(item,index) in bannerList" :key="index">
              <img :src="item" alt="">
            </div>
          </div>
        </div>
        <div class="spDetail">
          <div class="spDname">
            <h3>{{this.goodsInfo.goods_name}}</h3>
            <p><span>{{this.goodsInfo.goods_integral}}</span>积分</p>
          </div>
        </div>
        <div class="numcount">
          <span>数量</span>
          <van-stepper v-model="goodsNum" />
        </div>
        <div class="tablist">
          <div :class="typetab==0?'thislist':''"  @click="typetab=0">产品详情</div>
          <div :class="typetab==1?'thislist':''"  @click="typetab=1">规格参数</div>
        </div>
        <div class="wrap_content" v-show="typetab===0">
          <div class="spcontent" v-html="goodsInfo.goods_info">
          </div>
        </div>
        <div class="wrap_canshu" v-show="typetab===1">
          <div class="spcanshu" v-html="goodsInfo.goods_parameter"></div>
        </div>
        <a href="" class="kefu iconfont icon-kefu"></a>
      </div>
      <van-button type="default" class="duihuan" @click="toDingdan">立即兑换</van-button>
    </div>
</template>

<script>
  import { apiGet } from '../common/js/api';
  import Swiper from 'swiper'
    export default {
        name: "sp_xiangqin",
      data(){
        let params = this.$route.params;
          return{
            typetab:0,
            goodsNum:1,
            bannerList:[],
            goodsInfo:{
              "value":1,
              'id':'010',
              'title':'个人保险',
              'jifen':'888'
            }
          }
      },
      created(){
          console.log(this.$route.params,this.$route.params.id)
        this.getGoodsDetail()
      },
      mounted(){
        var mySwiper = new Swiper('.swiper-container', {
          autoplay: true,//可选选项，自动滑动
          loop : true,
          pagination: {
            el: '.swiper-pagination',
          },
        })
      },
      methods:{
        //获取商品详情
        getGoodsDetail(){
          apiGet("/goods/goods-details",{
            id:this.$route.params.id,
          }).then(res=>{
            this.goodsInfo = res.data.data.data;
            console.log(res)
            this.bannerList=res.data.data.data.goods_img;
          })
        },
        toDingdan(){
          this.$router.push({name:'sDingdan',params:{id:this.goodsInfo.id,title: this.goodsInfo.goods_name,jifen: this.goodsInfo.goods_integral,num:this.goodsNum}})
        }
      }
    }
</script>

<style lang="scss" >
  .swiper-pagination-bullet-active{
    width: 0.13rem !important;
    background: white !important;
    height: 0.06rem;
    border-radius: 0.03rem;
  }
  .swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction{
    bottom: 0.6rem;
  }
  .swiper-container{
    width: 100%;
    .swiper-slide>img{
      width: 100%;
      max-height: 2.56rem;
    }
  }
  .van-stepper{
    float: right;
  }
  .van-stepper__input{
    border: none;
  }
  .van-stepper__minus, .van-stepper__plus{
    background: #E5E8EB;
    border-radius:0.04rem;
    color: #ccc;
    text-align: center;
  }
  .spXiangqin{
    background: #E6E9ED;
    height: 100%;
    overflow: hidden;
    flex-direction: column;
    display: flex;
    .xiang_wrap{
      flex: 1;
      overflow: auto;
    }
    .duihuan{
      width: 100%;
      height: 0.47rem !important;
      background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
      font-size: 0.19rem;
      color: white;
    }
    .spDetail{
      width: 3.5rem;
      background: white;
      position: relative;
      font-size: 0.17rem;
      padding: 0.17rem 0.13rem;
      box-sizing: border-box;
      margin: 0 auto;
      top: -0.25rem;
      z-index: 3;
      border-radius: 0.04rem;
      h3{
        font-size: 0.19rem;
      }
      .spDname p{
        color: #CC1C1C;
      }
    }
    .numcount{
      width: 3.5rem;
      height: 0.5rem;
      background: white;
      margin: 0 auto;
      padding: 0.13rem;
      font-size: 0.17rem;
      position: relative;
      top: -0.05rem;
      margin-bottom: 0.15rem;
      border-radius: 0.04rem;
    }
    .tablist{
      width: 3.5rem;
      height: 0.47rem;
      background: white;
      margin:  0 auto;
      border-radius: 0.04rem;
      display: flex;
      div{
        flex: 1;
        background: #C4C8CF;
        font-size: 0.17rem;
        text-align: center;
        line-height: 0.47rem;
        color: white;
      }
      .thislist{
        background: white;
        color: #333;
      }
    }
    .wrap_content,.wrap_canshu{
      background: white;
      width: 3.5rem;
      overflow: auto;
      min-height: 3rem;
      margin: 0 auto;
      margin-bottom: 0.6rem;
      img{
        width:100%;
      }
      .spcontent,.spcanshu{
        width: 3.25rem;
        min-height: 2.6rem;
        margin:  0.13rem auto 0.17rem;
      }
    }

    .kefu{
      width: 0.52rem;
      height: 0.52rem;
      background:linear-gradient(-90deg,rgba(70,133,238,1),rgba(155,193,255,1));
      border-radius:50%;
      box-shadow:0px 5px 10px 0px rgba(60,127,238,0.42);
      position: fixed;
      bottom: 1.5rem;
      display: block;
      text-align: center;
      line-height: 0.52rem;
      font-size: 0.28rem;
      color: white;
      z-index: 3;
      right: 0.11rem;
    }
  }
</style>



